import get from 'lodash/get';

const paths = {
  home: '/',
  projects: '/projects',
  stack: '/stack',
  posts: '/posts',
  resume: '/resume',
  certificates: '/certificates',
};

export const externalLink = {
  github: 'https://github.com/guyandtheworld',
  twitter: 'https://twitter.com/guyandtheworld',
  medium: 'https://medium.com/@guyandtheworld',
  linkedin: 'https://www.linkedin.com/in/guyandtheworld/',
  instagram: 'https://www.instagram.com/guyandtheworld/',
  evalaicli: 'https://github.com/Cloud-CV/evalai-cli',
  snapdiet: 'https://github.com/guyandtheworld/snapdiet',
  makri: 'https://github.com/guyandtheworld/makri',
  arvy: 'https://github.com/guyandtheworld/ARvy/',
  recession: 'https://github.com/guyandtheworld/recession',
};

export default (pathName, languagePath) => {
  const path = get(paths, pathName);

  if (!path) {
    throw new Error(`Path '${pathName}' not found`);
  }

  return languagePath ? `/${languagePath}${path}` : `${path}`;
};



